body{
  background-color: lightskyblue;
}
h1{
  color: white;
  letter-spacing: 2px;
  font-size: 40px;
}
.msg{
  color: white;
  font-size: 30px;
}
.content{
  margin: 0px auto 25px auto;
  box-shadow: 0 5px 8px rgb(255, 255, 255);
  border-radius: 10px;
  background-color: white;
  padding: 20px 40px;
  width: fit-content;
}
.item {
  display: inline;
  margin-right: 10px;
  font-size: 30px;
  cursor: pointer;
  color: lightskyblue;
  text-transform: capitalize;
}
.bi{
  cursor: pointer;
  font-size: 20px;
  color: lightskyblue;
}
.finished{
  text-decoration: line-through;
  opacity: 0.5;
  transition: 1s;
  font-size: 20px;
}
.finshed-icon{
  font-size: 16px;
}
.error{
  font-size: 30px;
  color: red;
  margin-top: 10px;
}